import React, { useState } from "react";
import "bootstrap/dist/css/bootstrap.min.css";
import { Navbar, NavDropdown } from "react-bootstrap";
import "./Navbar.css";

export default function Nav_bar(props) {
  const [algorithm, setAlgorithm] = useState("");
  function setAlgo(algo) {
    document.getElementById("run").className = "list button";
    setAlgorithm(algo);
  }
  return (
    <div>
      <Navbar className="navbar" variant="k" expand="lg">
        <div className="cont">
          <div>
            <Navbar.Brand
              className="title"
              style={{ marginLeft: "20px", color: "white" }}
              href="#home"
            >
              Pathfinding Visualizer
            </Navbar.Brand>
          </div>
          <div className="buttons">
            <div
              style={{
                display: "flex",
                flexDirection: "row",
                justifyContent: "center",
                alignItems: "center",
              }}
            >
              <button className="list button" onClick={() => props.clear()}>
                Clear
              </button>
              <button
                className="list disabled"
                id="run"
                onClick={() => props.run(algorithm)}
              >
                Run {algorithm}!
              </button>
              <div style={{ display: "flex", flexDirection: "row" }}>
                <NavDropdown title={"Algorithms"} id="nav-dropdown">
                  <NavDropdown.Item disabled="true" href="#action/3.4">
                    Select an Algorithm
                  </NavDropdown.Item>
                  <NavDropdown.Divider />
                  <NavDropdown.Item
                    onClick={() => setAlgo("Dijkstra")}
                    href="#action/3.1"
                  >
                    Dijkstra
                  </NavDropdown.Item>
                  <NavDropdown.Item
                    onClick={() => setAlgo("Breath First Search")}
                    href="#action/3.2"
                  >
                    Breath First Search
                  </NavDropdown.Item>
                  <NavDropdown.Item
                    onClick={() => setAlgo("Depth First Search")}
                    href="#action/3.3"
                  >
                    Depth First Search
                  </NavDropdown.Item>
                </NavDropdown>
              </div>
            </div>
          </div>
        </div>
      </Navbar>
    </div>
  );
}
