export function animate(paths) {
  let { visitedNodes, path } = paths;
  for (let i = 0; i < visitedNodes.length; i++) {
    if (i === 0) {
      continue;
    }
    setTimeout(() => {
      if (i !== visitedNodes.length - 1) {
        document.getElementById(
          `${visitedNodes[i].row}-${visitedNodes[i].col}`
        ).className = "node visitedNode";
      }
    }, i * 10);
    setTimeout(() => {
      if (i === visitedNodes.length - 1) {
        animatePath(path);
      }
    }, i * 10);
  }
}
function animatePath(path) {
  for (let i = 0; i < path.length; i++) {
    if (i === 0) {
      continue;
    }
    setTimeout(() => {
      document.getElementById(`${path[i].row}-${path[i].col}`).className =
        "node path";
    }, i * 30);
  }
}
