import React, { Component } from "react";
import "./board.css";
import Nav from "../Navbar/Navbar";
import Node from "../Node/node";
import Option from "../Navbar/option";
import { Dijkstra } from "../Algorithms/Dijkstra";
import { animate } from "../animate";

export default class Board extends Component {
  constructor(props) {
    super(props);
    this.state = {
      board_array: [],
      mouseIsEntered: false,
      startNode: {
        m: Math.floor(
          Math.floor(window.innerWidth / 28) / 2 -
            Math.floor(window.innerWidth / 28) / 4
        ),
        n: Math.floor(Math.floor((window.innerHeight - 170) / 28) / 2),
      },
      finishNode: {
        m: Math.floor(
          Math.floor(window.innerWidth / 28) / 2 +
            Math.floor(window.innerWidth / 28) / 4
        ),
        n: Math.floor(Math.floor((window.innerHeight - 170) / 28) / 2),
      },
    };
    window.addEventListener("resize", () => {
      this.setState({
        startNode: {
          m: Math.floor(
            Math.floor(window.innerWidth / 25) / 2 -
              Math.floor(window.innerWidth / 25) / 4
          ),
          n: Math.floor(Math.floor((window.innerHeight - 170) / 28) / 2),
        },
        finishNode: {
          m: Math.floor(
            Math.floor(window.innerWidth / 25) / 2 +
              Math.floor(window.innerWidth / 25) / 4
          ),
          n: Math.floor(Math.floor((window.innerHeight - 170) / 28) / 2),
        },
      });
      this.reset();
    });
  }
  componentDidMount() {
    this.reset();
  }
  reset() {
    let m = Math.floor(window.innerWidth / 28);
    let n = Math.floor((window.innerHeight - 170) / 28);
    this.setState({
      isMouseEntered: false,
    });
    let array = [];
    for (let i = 0; i < m; i++) {
      let m_array = [];
      for (let j = 0; j < n; j++) {
        m_array.push({
          row: i,
          col: j,
          isWall: false,
          isStartNode:
            i === this.state.startNode.m && j === this.state.startNode.n,
          isFinishNode:
            i === this.state.finishNode.m && j === this.state.finishNode.n,
          previous: null,
          distance: Infinity,
        });
      }
      array.push(m_array);
    }
    this.setState({
      board_array: array,
    });
  }
  clear() {
    let m = Math.floor(window.innerWidth / 28);
    let n = Math.floor((window.innerHeight - 170) / 28);
    this.setState({
      isMouseEntered: false,
    });
    let array = [];
    for (let i = 0; i < m; i++) {
      let m_array = [];
      for (let j = 0; j < n; j++) {
        m_array.push({
          row: i,
          col: j,
          isWall: false,
          isStartNode:
            i === this.state.startNode.m && j === this.state.startNode.n,
          isFinishNode:
            i === this.state.finishNode.m && j === this.state.finishNode.n,
          previous: null,
          distance: Infinity,
        });
        if (i === this.state.startNode.m && j === this.state.startNode.n) {
          document.getElementById(`${i}-${j}`).className = "node startNode";
        } else if (
          i === this.state.finishNode.m &&
          j === this.state.finishNode.n
        ) {
          document.getElementById(`${i}-${j}`).className = "node finishNode";
        } else {
          document.getElementById(`${i}-${j}`).className = "node";
        }
      }
      array.push(m_array);
    }
    this.setState({
      board_array: array,
    });
  }
  plotWall(m, n) {
    let array = this.state.board_array;
    let node = array[m][n];
    let newNode = {
      ...node,
      isWall: !node.isWall,
    };
    array[m][n] = newNode;
    return array;
  }
  mouseDown(m, n) {
    let newArray = this.plotWall(m, n);
    this.setState({
      board_array: newArray,
      mouseIsEntered: true,
    });
  }
  mouseUp() {
    this.setState({
      mouseIsEntered: false,
    });
  }
  mouseEnter(m, n) {
    if (!this.state.mouseIsEntered) return;
    let newArray = this.plotWall(m, n);
    this.setState({
      board_array: newArray,
    });
  }
  run(algo) {
    if (algo === " ") {
      return;
    } else if (algo === "Dijkstra") {
      let dijkstra = Dijkstra(
        this.state.startNode,
        this.state.finishNode,
        this.state.board_array
      );
      animate(dijkstra);
    } else if (algo === "Breath First Search") {
      console.log(algo);
    } else if (algo === "Depth First Search") {
      console.log(algo);
    }
  }
  render() {
    return (
      <React.Fragment>
        <Nav clear={() => this.clear()} run={(algo) => this.run(algo)} />
        <Option />
        <div className="col">
          {this.state.board_array.map((row, rowIdx) => {
            return (
              <div key={rowIdx} className="c">
                {row.map((col, colIdx) => {
                  return (
                    <Node
                      row={rowIdx}
                      col={colIdx}
                      key={rowIdx - colIdx}
                      mouseUp={() => this.mouseUp()}
                      mouseEnter={(m, n) => this.mouseEnter(m, n)}
                      mouseDown={(m, n) => this.mouseDown(m, n)}
                      isWall={col.isWall}
                      isStartNode={col.isStartNode}
                      isFinishNode={col.isFinishNode}
                    />
                  );
                })}
              </div>
            );
          })}
        </div>
      </React.Fragment>
    );
  }
}
